/* eslint-disable import/no-extraneous-dependencies */
import { css, html, LitElement } from 'lit';
import { property, query, state } from 'lit/decorators.js';

import '@material/mwc-select';
import '@openscd/oscd-tree-grid';
import type { Select } from '@material/mwc-select';
import type { Tree, TreeGrid } from '@openscd/oscd-tree-grid';

function enumTypeObject(enumDa: Element): Tree {
  const tree: Tree = {};
  const children: Tree = {};

  const enumType = enumDa.ownerDocument.querySelector(
    `EnumType[id="${enumDa.getAttribute('type')}"]`
  );
  if (!enumType) return tree;

  Array.from(enumType.querySelectorAll('EnumVal')).forEach(enumVal => {
    const enumValContent = enumVal.textContent ?? 'UNKNOWN_EnumVal';

    children[enumValContent] = {};
  });

  tree.children = children;
  return tree;
}

function subDataAttributeObject(bda: Element): Tree {
  const tree: Tree = {};
  const children: Tree = {};

  const daType = bda.ownerDocument.querySelector(
    `DAType[id="${bda.getAttribute('type')}"]`
  );
  if (!daType) return tree;

  Array.from(daType.querySelectorAll('BDA')).forEach(childBda => {
    const bdaName = childBda.getAttribute('name') ?? 'UNKNOWN_BDA';

    if (bda.getAttribute('bType') === 'Struct')
      children[bdaName] = subDataAttributeObject(childBda);
    else if (bda.getAttribute('bType') === 'Enum')
      children[bdaName] = enumTypeObject(childBda);
    else children[bdaName] = {};
  });

  tree.children = children;
  return tree;
}

function dataAttributeObject(da: Element): Tree {
  const tree: Tree = {};
  const children: Tree = {};

  const daType = da.ownerDocument.querySelector(
    `DAType[id="${da.getAttribute('type')}"]`
  );
  if (!daType) return tree;

  Array.from(daType.querySelectorAll('BDA')).forEach(bda => {
    const bdaName = bda.getAttribute('name') ?? 'UNKNOWN_BDA';
    if (bda.getAttribute('bType') === 'Struct')
      children[bdaName] = subDataAttributeObject(bda);
    else if (bda.getAttribute('bType') === 'Enum')
      children[bdaName] = enumTypeObject(bda);
    else children[bdaName] = {};
  });

  tree.children = children;
  return tree;
}

function subDataObjectsObject(sdo: Element): Tree {
  const tree: Tree = {};
  const children: Tree = {};

  const doType = sdo.ownerDocument.querySelector(
    `DOType[id="${sdo.getAttribute('type')}"]`
  );
  if (!doType) return tree;

  Array.from(doType.querySelectorAll('SDO,DA')).forEach(sDoOrDa => {
    if (sDoOrDa.tagName === 'SDO') {
      const sDoName = sDoOrDa.getAttribute('name') ?? 'UNKNOWN_SDO';
      children[sDoName] = subDataObjectsObject(sDoOrDa);
    } else {
      const daName = sDoOrDa.getAttribute('name') ?? 'UNKNOWN_DA';
      if (sDoOrDa.getAttribute('bType') === 'Struct') {
        children[daName] = dataAttributeObject(sDoOrDa);
      } else if (sDoOrDa.getAttribute('bType') === 'Enum') {
        children[daName] = enumTypeObject(sDoOrDa);
      } else children[daName] = {};
    }
  });

  tree.children = children;
  return tree;
}

function dataObjectObject(dO: Element): Tree {
  const tree: Tree = {};
  const children: Tree = {};

  const doType = dO.ownerDocument.querySelector(
    `DOType[id="${dO.getAttribute('type')}"]`
  );
  if (!doType) return tree;

  Array.from(doType.querySelectorAll('SDO,DA')).forEach(sDoOrDa => {
    if (sDoOrDa.tagName === 'SDO') {
      const sDoName = sDoOrDa.getAttribute('name') ?? 'UNKNOWN_SDO';
      children[sDoName] = subDataObjectsObject(sDoOrDa);
    } else {
      const daName = sDoOrDa.getAttribute('name') ?? 'UNKNOWN_DA';
      if (sDoOrDa.getAttribute('bType') === 'Struct') {
        children[daName] = dataAttributeObject(sDoOrDa);
      } else if (sDoOrDa.getAttribute('bType') === 'Enum') {
        children[daName] = enumTypeObject(sDoOrDa);
      } else children[daName] = {};
    }
  });

  tree.children = children;
  return tree;
}

function anyLnObject(anyLn: Element): Tree {
  const tree: Tree = {};
  const children: Tree = {};

  const lnType = anyLn.ownerDocument.querySelector(
    `LNodeType[id="${anyLn.getAttribute('lnType')}"]`
  );
  if (!lnType) return tree;

  Array.from(lnType.querySelectorAll('DO')).forEach(dO => {
    const doName = dO.getAttribute('name') ?? 'UNKNOWN_DO';

    children[doName] = dataObjectObject(dO);
  });

  tree.children = children;
  return tree;
}

function lDeviceObject(lDevice: Element): Tree {
  const tree: Tree = {};
  const children: Tree = {};

  Array.from(lDevice.querySelectorAll('LN0,LN')).forEach(anyLn => {
    const anyLnClass = `${anyLn.getAttribute('prefix') ?? ''} ${
      anyLn.getAttribute('lnClass') ?? 'UNKNOWN_INST'
    } ${anyLn.getAttribute('inst') ?? ''}`;

    children[anyLnClass] = anyLnObject(anyLn);
  });

  tree.children = children;

  return tree;
}

function accPointObject(accP: Element): Tree {
  const tree: Tree = {};
  const children: Tree = {};

  Array.from(accP.querySelectorAll('LDevice')).forEach(lDevice => {
    const lDeviceInst = lDevice.getAttribute('inst') ?? 'UNKNOWN_INST';

    children[lDeviceInst] = lDeviceObject(lDevice);
    children[lDeviceInst]!.mandatory = true;
  });

  tree.children = children;
  return tree;
}

function iedObject(ied: Element): Tree {
  const tree: Tree = {};
  const children: Tree = {};

  Array.from(ied.querySelectorAll('AccessPoint')).forEach(accPoint => {
    const accPointName = accPoint.getAttribute('name') ?? 'UNKNOWN_ACCP';

    children[accPointName] = accPointObject(accPoint);
    children[accPointName]!.mandatory = true;
  });

  tree.children = children;
  return tree;
}

/**  */
export default class SclIedBrwoserPlugin extends LitElement {
  /** The document being edited as provided to plugins by [[`OpenSCD`]]. */
  @property({ attribute: false })
  doc!: XMLDocument;

  /** SCL change indicator */
  @property({ type: Number })
  editCount = -1;

  @state()
  get ieds(): Element[] {
    return Array.from(this.doc.querySelectorAll(':root > IED'));
  }

  @state()
  get firstIed(): string {
    return this.doc.querySelector(':root > IED')?.getAttribute('name') ?? '';
  }

  @state()
  selectedIed: Element | null = null;

  @query('oscd-tree-grid') tree!: TreeGrid;

  async selectIed(e: Event): Promise<void> {
    const selectedIedName = (e.target as Select).selected?.value;
    this.selectedIed = this.doc.querySelector(
      `:root > IED[name="${selectedIedName}"]`
    );
    await this.requestUpdate();

    this.tree.filter = '';
    await this.tree.requestUpdate();
    await this.requestUpdate();
  }

  iedTree(): Tree {
    const tree: Tree = {};
    if (!this.selectedIed) return tree;

    const iedName = this.selectedIed?.getAttribute('name') ?? 'UNKNOWN';
    tree[iedName] = iedObject(this.selectedIed);
    tree[iedName]!.mandatory = true;
    return tree;
  }

  @state()
  render() {
    return html`<div>
      <mwc-select value="${this.firstIed}" @selected="${this.selectIed}"
        >${this.ieds.map(
          ied =>
            html`<mwc-list-item
              twoline
              value="${ied.getAttribute('name') ?? 'UNDEFINED'}"
              ><slot>${ied.getAttribute('name') ?? 'UNDEFINED'}</slot
              ><slot slot="secondary">${ied.getAttribute('vendor')}</slot>
            </mwc-list-item>`
        )}</mwc-select
      >
      <oscd-tree-grid .tree=${this.iedTree()}></oscd-tree-grid>
    </div>`;
  }

  static styles = css`
    div {
      padding: 10px;
    }
  `;
}
